.collapsible-container {
  display: flex;
  flex-direction: column;
}

.collapsible-head {
  background: #ddd;
  cursor: pointer;
  font-weight: bold;
  padding: 20px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.collapsible-body {
  background: #eee;
  display: none;
  padding: 20px;
}

.collapsible-body.is-open {
  display: block;
}
